label.error {
  background: #820705;
  border: 1px solid transparent;
  -moz-border-radius: 5px;
  border-radius: 5px;
  font-size: 11px !important;
  line-height: 12px;
  padding: 3px;
  color: #fff;
  position: absolute;
  top: -26px;
  width: 131px;
  left: 18px;
}


label.error:after {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(130, 7, 5, 0);
  border-top-color: #820705;
  border-width: 6px;
  margin-left: -10px;
}
