.g-recaptcha{
  margin: 0 auto 9px;
  position: relative;
  width: 304px;
  left: -11px;
  top: 14px;
  height: 76px;
  overflow: hidden;
}
.g-recaptcha iframe{
  border-radius: 32px;
  width: 299px;
  height: 74px;
}
.g-recaptcha:after{
  content: '';
  background: #f3f3f3;
  position: absolute;
  top: 0;
  left: 2px;
  width: 2px;
  height: 100%;
}
.g-recaptcha > div{
  position: relative;
  top: -2px;
}


